// libs
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

// widgets
import ButtonStatus from "components/ButtonStatus";
import CheckBoxBody from "components/MultiCheckBox/CheckBoxBody";
import CheckBoxHeader from "components/MultiCheckBox/CheckBoxHeader";
import SortIcon from "components/SortIcon";

// assets
import { Images } from "shared/Images";

// consts
import { ADMIN_BASE_ROUTE, CANDIDATE_TAB_VIEW, JOB_LIST_VIEW, LOCUM_ADMIN_ROUTES, STRINGS } from "shared/Constants";
import moment from "moment";

const TableView = ({
    data = [],
    multipleSelectedData = [],
    setMultipleSelectedData,
    toggleDeleteModal,
    tableHeadings = [],
    sortData = {},
    allDataSelected = false,
    activeTab = 0,
    onViewClick=()=>{},
    toggleAcceptModal=()=>{}
}) => {
    const history = useHistory();
    const [isSelectAll, setIsSelectAll] = useState(allDataSelected || false);

    return (
        <>
            <thead>
                <tr>
                    <th>
                        <CheckBoxHeader
                            isSelectAll={allDataSelected || isSelectAll}
                            setMultipleSelectedData={setMultipleSelectedData}
                            setIsSelectAll={setIsSelectAll}
                            data={data}
                            multipleSelectedData={multipleSelectedData}
                            disabled={!data?.length}
                        />
                    </th>
                    {tableHeadings.map((i, idx) => (
                        <th scope="col" key={idx}>
                            <span> {i.title} </span>
                            {i.sortKey && (
                                <SortIcon
                                    toggleIcon={sortData[i.sortKey]}
                                    onClick={() => i.action(i.sortKey, sortData[i.sortKey])}
                                />
                            )}
                        </th>
                    ))}
                </tr>
            </thead>

            {data?.length ? (
                data?.map((value, index) => (
                    <tbody key={index}>
                        <tr>
                            <td width={80}>
                                <CheckBoxBody
                                    setMultipleSelectedData={setMultipleSelectedData}
                                    setIsSelectAll={setIsSelectAll}
                                    value={value}
                                    multipleSelectedData={multipleSelectedData}
                                />
                            </td>
                            {/* <td>{index + 1}</td> */}
                            <td>{value?.candidate_name || "N/A"}</td>
                            <td>{moment(value?.applied_date).format("DD MMM YYYY") || "N/A"}</td>
                            <td>{value?.branch || "N/A"}</td>
                            <td>{` ${value?.rate}`}</td>
                            <td>{moment(value?.end_job_date).format("DD MMM YYYY") || "N/A"}</td>
                            <td width={200}>
                                <div className="d-flex btn_group">
                                    <ButtonStatus
                                        image={Images.EyeIcon}
                                        title={"Delete"}
                                        status={"secondary"}
                                        onClick={() => {
                                            onViewClick(value)
                                        }}
                                    />

                                    {activeTab == CANDIDATE_TAB_VIEW.APPLIED.ID ?<ButtonStatus
                                        image={Images.whiteTick}
                                        title={""}
                                        status={"Verified"}
                                        onClick={() => {
                                            toggleAcceptModal(value);
                                        }}
                                    /> : null}
                                    {/* <ButtonStatus
                                        image={Images.CloseWhiteIcon}
                                        title={"Delete"}
                                        status={"danger"}
                                        onClick={() => {
                                            toggleDeleteModal(value);
                                        }}
                                    /> */}
                                </div>
                            </td>
                        </tr>
                    </tbody>
                ))
            ) : (
                <tbody>
                    <tr>
                        <td
                            colSpan={8}
                            className="text-center py-5"
                        >
                           {STRINGS.NO_DATA_AVAILABLE}
                        </td>
                    </tr>
                </tbody>
            )}
        </>
    );
};

export default TableView;
