import BreadCrumb from 'components/BreadCrumb';
import React, { useEffect, useState } from 'react';
import {
  useHistory,
  useLocation,
} from 'react-router-dom/cjs/react-router-dom.min';
import { ADMIN_ROUTES, JOB_APPLICATION_STATUS, SNACK_MESSAGES, SNACK_VARIANT } from 'shared/Constants';
import Profile from 'views/authenticated/Profile/Profile';
import { VACANCY } from '../constants';
import useGetIdFromPath from 'hooks/Utilities/useGetIdFromPath';
import { useDispatch } from 'react-redux';
import { acceptRejectJobApplication, getCandidateDetails } from 'redux/Actions/PharmacyAdminDashboard';
import useNotification from 'hooks/Notifications';
import AcceptModal from 'components/AcceptModal';

const ViewProfile = () => {
  const history = useHistory();
  const location = useLocation();
  const dispatch = useDispatch();
  const id = useGetIdFromPath();  
  const [msg, sendNotification] = useNotification();
  const [locumeDetails, setLocumDetails] = useState({});
  const [showAcceptModal, setShowAcceptModal] = useState(false);

  const handleRejectUser = () => {};

  const toggleAcceptModal = (value) => {
    setShowAcceptModal((prevState) => !prevState);
  };

  const handleApproveUser = () => {
    let payload = {
      job_application_id: [id],
      job_application_status: JOB_APPLICATION_STATUS.ACCEPT
    };
    console.log(payload, 'delete branch data');
    dispatch(
      acceptRejectJobApplication(
        payload,
        (response) => {
          sendNotification({
            msg: response?.message,
            variant: SNACK_VARIANT.SUCCESS,
          });
          toggleAcceptModal();
          history.push(
            `${ADMIN_ROUTES.CANDIDATE_LIST.replace(
              ':id',
              location.pathname.split('/')[4]
            )}`
          );
        },
        (err) => {
          sendNotification({
            msg: err ? err : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
          toggleAcceptModal();
        }
      )
    );
  };
  console.log(locumeDetails, 'locumDetails',id);
  const fetchCandidateDetailsCall = () => {
    let payload = {
      id,
    };
    dispatch(
      getCandidateDetails(payload, (res = {}) => {
        console.log(res, "rteadffadfads")
        setLocumDetails(res, 'branch_name');
      })
    );
  };

  const navigateLinks = [
    {
      link: VACANCY.CANDIDATE_LIST,
      clickHandler: () => {
        history.push(
          `${ADMIN_ROUTES.CANDIDATE_LIST.replace(
            ':id',
            location.pathname.split('/')[4]
          )}`
        );
        // history.push(`admin/vacancies/view-vacancy/${location.pathname.split("/")[4]}/candidate-list`);
      },
    },
  ];

  useEffect(() => {
    if(id){
        fetchCandidateDetailsCall();
    }
  }, [id]);

  return (
    <>
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() => history.push(ADMIN_ROUTES.VACANCIES)}
          previousLink={VACANCY.VACANCIES}
          currentLink={VACANCY.LOCUM_PROFILE}
          navigateLinks={navigateLinks}
        />
        <div className="btn_group">
          {/* <button
            className="btn btn-sm btn-secondary"
            onClick={handleEditVacancy}
          >
            Reject
          </button> */}
          <button className="btn btn-sm btn-primary" onClick={toggleAcceptModal}>
            Approve
          </button>
        </div>
      </div>
      {Object.keys(locumeDetails).length ? (
        <Profile
          personalData={{}}
          profileData={locumeDetails}
          hideCSinUse={true}
          hideBankDetails={true}
          isEditable={false}
          isLocumUserProfile={false}
        />
      ) : null}
      <AcceptModal
        show={showAcceptModal}
        toggleModal={toggleAcceptModal}
        onClick={handleApproveUser}
        medium={true}
      />
    </>
  );
};

export default ViewProfile;
